






import { Chart, registerables } from "chart.js";
import { Component, Vue } from "vue-property-decorator";

Chart.register(...registerables)

@Component({
  name: "BarChart",
})
export default class BarChart extends Vue {
  fillData(): Chart<"bar", number[], string> {
    const canvas = document.getElementById("barchart") as HTMLCanvasElement
    const ctx = canvas.getContext("2d") as CanvasRenderingContext2D
    var mychart = new Chart(ctx, {
      type: "bar",
      data: {
        labels: [ "데이터 수", "정확도" ],
        datasets: [
        {
          label: "사용중인 모델",
          borderWidth: 1,
          borderColor: [
            "rgb(25, 118, 210)",
          ],
          backgroundColor: [
            "rgb(25, 118, 210, 0.2)",
          ],
          data: [100, 80],
        },
        {
          label: "재학습된 모델",
          borderWidth: 1,
          borderColor: [
            "rgb(76, 175, 80)"
          ],
          backgroundColor: [
            "rgb(76, 175, 80, 0.2)",
          ],
          data: [127, 85],
        }],
      },
      options: {
        maintainAspectRatio: false,
        plugins: {
          legend: {
            display: true,
          },
          tooltip: {
            backgroundColor: "#ffffff",
            borderColor: "#616161",
            borderWidth: .5,
            padding: {
              left: 15,
              right: 15,
              top: 8,
              bottom: 8,
            },
            caretSize: 0,
            // title
            titleColor: "#616161",
            titleAlign: "center",
            titleMarginBottom: 15,
            titleFont: {
              size: 16
            },
            // body
            bodyColor: "#616161",
            bodyAlign: "center",
            bodyFont: {
              size: 14
            },
            usePointStyle: true,
            callbacks: {
              labelPointStyle: function() {
                return {
                  pointStyle: "circle",
                  rotation: 0
                }
              },
            }
          }
        },
        scales: {
          x: {
            grid: {
              drawBorder: false,
            },
            ticks: {
              padding: 8,
              font: {
                size: 13,
              }
            },
          },
          y: {
            beginAtZero: true,
            suggestedMax: 10,
            grid: {
              drawBorder: false,
              lineWidth: function(context) {
                if (context.tick.value == 0) {
                  return 3
                } else {
                  return .5
                }},
              color: function(context) {
                if (context.tick.value == 0) {
                  return "rgba(158, 176, 186, 1)"
                } else {
                  return "rgba(158, 176, 186, 0.4)"
                }},
            },
          },
        }
      }
    })

    return mychart
  }

  mounted(): void {
    this.fillData()
  }
}

