







































































import { Component, Vue, Prop } from "vue-property-decorator";

interface PatientInterface {
  id: number;
  name: string;
  age: number;
  sex: string;
  adm_time: number;
  iculos: number;
  room: string;
  bed: string;
  reg_date: Date;
  reg_id: string;
  upd_date: Date;
  upd_id: string;
  emp_id: string;
  ai_detect: string;
}

interface VitalInterface {
  id: number;
  time: Date;
  hr: number;
  o2sat: number;
  temp: number;
  sbp: number;
  map: number;
  dbp: number;
  resp: number;
  etco2: number;
  train: string;
  patient_id: number;
}

@Component({
    name: "HuSisVitalDetail",
})
export default class HuSisVitalDetail extends Vue {
  // 클릭한 환자 정보 Prop
  @Prop() public vitalProp!: PatientInterface

  // 생체 정보 이력
  vitals = this.$store.state.husisStore.vitals;
  vitalDetail: Array<VitalInterface> = [];

  mounted(): void {
    this.$store.state.husisStore.vitalProp = this.vitalProp.id;

    for (const vital of this.vitals) {
      if (vital.patient_id === this.vitalProp.id) {
        this.vitalDetail.push(vital);
      }
    }
    this.$store.commit("setVitalDetail", this.vitalDetail);
  }
}
