





































































import { Component, Vue} from "vue-property-decorator";
import BarChart from "./precharts/PreBarChart.vue";
import BarChartHorizontal from "./precharts/PreBarChartHorizontal.vue";
import StackedBarChart from "./precharts/PreBarChartStacked.vue";
import FloatingBarChart from "./precharts/PreBarChartFloating.vue";

import LineChart from "./precharts/PreLineChart.vue";
import BarLineChart from "./precharts/PreBarLineChart.vue";

import RadarChart from "./precharts/PreRadarChart.vue";

import PieChart from "./precharts/PrePieChart.vue";
import DoughnutChart from "./precharts/PreDoughnutChart.vue";
import PolarAreaChart from "./precharts/PrePolarAreaChart.vue";
import MultiPieChart from "./precharts/PrePieMultiChart.vue";

import BubbleChart from "./precharts/PreBobbleChart.vue";
import ScatterChart from "./precharts/PreScatterChart.vue";


@Component({
  name: "SepsisCharts",
  components: {
    BarChart,
    BarChartHorizontal,
    StackedBarChart,
    FloatingBarChart,

    LineChart,
    BarLineChart,

    RadarChart,

    PieChart,
    DoughnutChart,
    PolarAreaChart,
    MultiPieChart,

    BubbleChart,
    ScatterChart,
  },
})
export default class SepsisCharts extends Vue{
  // window size에 따른 main body padding 변화
  // 좌 우 다르게 줌 (스크롤 ui 때문)
  rePaddingL = "1%";
  rePaddingR = "2%";
  created(): void {
    this.onResize()
    window.addEventListener("resize", this.onResize);
  }

  onResize(): void {
    if (window.innerWidth > 1264) {
      this.rePaddingL = "1%";
      this.rePaddingR = "2%";
    } else {
      this.rePaddingL = "5%";
      this.rePaddingR = "6%";
    }
  }
}
