






import { Chart, registerables } from "chart.js";
import { Component, Vue} from "vue-property-decorator";


Chart.register(...registerables)


@Component({
  name: "LineChart",
})
export default class LineChart extends Vue {
  setDate(ago: number): string {
    const newDate = new Date()
    const now = new Date(newDate.getFullYear(), newDate.getMonth(), newDate.getDate() + ago)
    var year = now.getFullYear().toString().slice(-2)
    var month = `0${(now.getMonth()+1).toString()}`.slice(-2);
    var date = `0${now.getDate().toString()}`.slice(-2);
    var weeks =["일", "월", "화", "수", "목", "금", "토"];
    var week = weeks[now.getDay()]

    var dates = `${year}.${month}.${date}`
    var weekss = `(${week})`

    return dates + weekss
  }

  chartRender(): Chart<"line", number[], string> {
    const canvas = document.getElementById("linechart") as HTMLCanvasElement
    const ctx = canvas.getContext("2d") as CanvasRenderingContext2D
    var mychart = new Chart(ctx, {
      type: "line",
      data: {
        labels: [ this.setDate(-6), this.setDate(-5), this.setDate(-4), this.setDate(-3), this.setDate(-2), this.setDate(-1), this.setDate(0) ],
        datasets: [
        {
          label: "안전",
          borderWidth: 1,
          borderColor: "#43A047",
          backgroundColor: "#C8E6C9",
          data: [ 10, 11, 10, 11, 13, 9, 16 ],
        },
        {
          label: "의심",
          borderWidth: 1,
          borderColor: "#F57C00",
          backgroundColor: "#FFE0B2",
          data: [ 5, 5, 7, 5, 2, 14, 9 ],
        },
        {
          type: "line",
          label: "위험",
          borderWidth: 1,
          borderColor: "#EF5350",
          backgroundColor: "#FFCDD2",
          data: [ 2, 1, 0, 1, 2, 4, 2 ],
        }
        ],
      },
      options: {
        maintainAspectRatio: false,
        plugins: {
          legend: {
            display: false
          },
          tooltip: {
            backgroundColor: "#ffffff",
            borderColor: "#616161",
            borderWidth: .5,
            padding: {
              left: 15,
              right: 15,
              top: 8,
              bottom: 8,
            },
            position: "nearest",
            caretSize: 0,
            // title
            titleColor: "#616161",
            titleAlign: "center",
            titleMarginBottom: 15,
            titleFont: {
              size: 16
            },
            // body
            bodyColor: "#616161",
            bodyAlign: "center",
            bodySpacing: 10,
            bodyFont: {
              size: 14
            },
            displayColors: true,
            usePointStyle: true,
            callbacks: {
              labelPointStyle: function() {
                return {
                  pointStyle: "circle",
                  rotation: 0
                }
              },
            }
          }
        },
        interaction: {
          mode: "index",
          intersect: false,
        },
        hover: {
          mode: "index",
        },
        elements: {
          point: {
            radius: 4,
            hoverRadius: 8,
          },
          line: {
            tension: .3
          }
        },
        scales: {
          x: {
            grid: {
              drawBorder: false,
              borderDash: [ 3, 3 ],
              color: "rgba(158, 176, 186, 0.4)",
              lineWidth: function(context) {
                if (context.tick.value == 0) {
                  return 1
                } else {
                  return 1
                }
              },
            },
            ticks: {
              padding: 8,
              font: {
                size: 13,
              }
            }
          },
          y: {
            beginAtZero: true,
            suggestedMax: 10,
            grid: {
              drawBorder: false,
              lineWidth: function(context) {
                if (context.tick.value == 0) {
                  return 3
                } else {
                  return .5
                }},
              color: function(context) {
                if (context.tick.value == 0) {
                  return "rgba(158, 176, 186, 1)"
                } else {
                  return "rgba(158, 176, 186, 0.4)"
                }},
            },
            ticks: {
            },
          },
        }
      }
    })

    return mychart
  }

  mounted(): void {
    this.chartRender()
  }
}
