


























































import { Component, Vue} from 'vue-property-decorator';
import axios, { AxiosResponse } from 'axios'

interface KeyboardEvent {
  keyCode: number;
}

@Component({
  name: 'ResetPassword'
})

export default class ResetPassword extends Vue {
  valid = false
  password = ""
  passwordConfirm = ""
  email = ""

  resetPassword(): void {
    if (!this.password || !this.passwordConfirm) {
      alert('Check the input value')
    } else {
      axios.put('accounts/resetpassword', {email: this.$route.params.email, password: this.password, passwordconfirm: this.passwordConfirm})
        .then((res: AxiosResponse) => {
          // console.log(res)
          alert(res.data.message)
          this.$router.push({name: 'Accounts'})
        })
    }
  }

  // 새로고침 방지
  created(): void {
    document.onkeydown = this.lockF5
  }
  beforeDestroy():void {
    document.onkeydown = this.unlockF5
  }

  lockF5(event: KeyboardEvent): boolean|undefined {
    if (event.keyCode == 116) {
      alert('Not allowed')
      return false
    }
  }
  unlockF5(event: KeyboardEvent): boolean|undefined {
    if (event.keyCode == 116) {
      return true
    }
  }
}

