





















































import { Component, Vue, Watch } from "vue-property-decorator";

@Component({
  name: "PRCenterPost",
  components: {},
})
export default class PRCenterPost extends Vue {
  boardItems = ['공지사항', '보도자료', '홍보자료'];
  image = null;
  imageList = [];
  url = ""; // 업로드 이미지 url

  @Watch("image")
  imageChange(): void {
    if (this.image != null) {
      console.log('!null')
    } else {
      console.log('null')
    }
  }

  imagecheck(): void {
    console.log(this.image);
    console.log(this.url);
  }
}
