

































































































































































































































































































































































































import { Component, Vue, Prop } from "vue-property-decorator";
import HuSisChart from "./HuSisAIRetrainChart.vue";
// import HuSisChartScroes from "./HuSisAIRetrainChartScores.vue";

@Component({
  name: "HuSisAIStatic",
  components: {
    HuSisChart,
    // HuSisChartScroes,
  }
})
export default class HuSisAIStatic extends Vue{
  @Prop() public curruntModel!: { perform: number; usedData: number; f1Score: number; loss: number; }

  // 현재시간 - 새 모델이 적용된 후 모델 이력에 남는 날짜
  date = "";
  week = "";
  time = "";

  mounted(): void {
    this.setDate();
    setInterval(this.setDate.bind(this), 1000)
  }

  setDate(): void {
    var now = new Date()
    var year = now.getFullYear().toString().slice(-2)
    var month = `0${(now.getMonth()+1).toString()}`.slice(-2);
    var date = `0${now.getDate().toString()}`.slice(-2);
    var weeks =["일", "월", "화", "수", "목", "금", "토"];
    var week = weeks[now.getDay()]
    var hours = `0${now.getHours().toString()}`.slice(-2)
    var mins = `0${now.getMinutes().toString()}`.slice(-2)
    var sec = `0${now.getSeconds().toString()}`.slice(-2)

    this.date = `${year}.${month}.${date}`
    this.week = `${week}`
    this.time = `${hours}:${mins}:${sec}`
  }

  confirmDialog = false; // 재학습 진행 여부 dialog
  dialog = false; // 재학습 진행 dialog
  selectConfirmDialog = false;
  progressSize = 100; // progress circle 크기
  value = 0; // progress 진행 값
  interval = 0; // 반복함수
  unUsedDataCnt = this.$store.getters.untrainedDataCount; // 미학습 데이터 수
  currunt = "currunt";
  reTrained = "re";
  selectModel = "";
  done = false;

  // 재학습한 모델 정보                                                                                                                                
  reTrainedModel = {                                                                         
    perform: this.curruntModel.perform + 5,                                                                                                                                                                                                                                                                                   
    usedData: this.curruntModel.usedData + this.unUsedDataCnt,                                                                                                         
    f1Score: 0.86,         
    loss: 0.2123,
  }
  
  // 재학습 진행 여부 O
  confirmRetrainAccept(): void {
    this.dialog = true;
    this.confirmDialog = false;
  }
  
  // 재학습 진행 여부 X
  confirmRetrainReject(): void {
    this.confirmDialog = false;
  }

  // 재학습 진행중 progress circle
  progressValue(): void {
    this.interval = setInterval(() => {
      if(this.value === 100) {
        this.progressSize = 0;
        clearInterval(this.interval)
        return (this.value = 0)
      }
      this.value += 10
    }, 500)
  }

  // 모델 선택 확인
  selectConfirm(arg: string): void {
    if (arg == "currunt") {
      this.selectConfirmDialog = true
      this.selectModel = "사용중인"
    } else {
      this.selectConfirmDialog = true
      this.selectModel = "재학습된"
    }
  }

  selectModelConfirm(arg: string): void {
    if (arg == "사용중인") {
      this.useCurruntModel()
      this.selectConfirmDialog = false
    } else {
      this.useReTrainedModel()
      this.selectConfirmDialog = false
    }
  }

  // 현재 모델 계속 사용함
  useCurruntModel(): void {
    this.dialog = false;
    this.done = true;
    this.progressSize = 100;
  }

  // 새로운 모델 사용함
  useReTrainedModel(): void {
    this.dialog = false;
    this.done = true;
    this.progressSize = 100;
    this.$store.dispatch("setTrained")
      .then(() => {
        this.unUsedDataCnt = this.$store.getters.untrainedDataCount;
      })
    this.$emit("emitEvent", this.reTrainedModel, this.date)
  }
}
