import Vue from 'vue'
import VueRouter, {Route} from 'vue-router';

import HuSis from '@/components/hugo/sepsis/HuSis.vue';
import HuSisDashboard from '@/components/hugo/sepsis/dashboard/HuSisDashBoard.vue';
import HuSisPatientManagement from '@/components/hugo/sepsis/patientmanage/HuSisPatientManagement.vue';
import HuSisPreCharts from '@/components/hugo/sepsis/utils/PreCharts.vue';
import HuSisAIManagement from '@/components/hugo/sepsis/aimanage/HuSisAIManagement.vue';
import HuSisBloodTest from '@/components/hugo/sepsis/bloodtest/HuSisBloodTest.vue';

const HuSisMainGuard = (to: Route, from: Route, next: (url: string | void | undefined) => void): void => {
  // console.log(from.name, '여기서 와서')
  // console.log(to.name, '로 간다.')
  // console.log('=========')
  if (to.name === "HuSis") {
    next('/hugo/sepsis/dashboard')
  } else {
    next()
  }
}

Vue.use( VueRouter)

export default [
  {
    path: '/hugo/sepsis',
    name: 'HuSis',
    component: HuSis,
    beforeEnter: HuSisMainGuard,
    children: [
      // sepsis main
      { path: 'dashboard', name:'sepsisDashboard', component: HuSisDashboard },
      { path: 'patientmanage', name:'sepsisPatientmanage', component: HuSisPatientManagement },
      { path: 'bloodtest', name:'sepsisBloodTest', component: HuSisBloodTest },
      { path: 'aimanage', name:'SepsisAIManagement', component: HuSisAIManagement },
      // utils
      { path: 'precharts', name:'SepsisPreCharts', component: HuSisPreCharts },
    ],
  },
]