











import { Component, Vue } from "vue-property-decorator";
import HuSisStatics from "./HuSisPatientStatics.vue"; // 데이터
import HuSisPatientList from "./HuSisPatientList.vue"; // 입원 환자 정보, 목록


@Component({
  name: "HuSisPatientManagement",
  components: {
    HuSisStatics,
    HuSisPatientList,
  }
})
export default class HuSisPatientManagement extends Vue{
  // window size에 따른 main body padding 변화
  // 좌 우 다르게 줌 (스크롤 ui 때문)
  rePaddingL = "1%";
  rePaddingR = "2%";
  created(): void {
    this.onResize()
    window.addEventListener('resize', this.onResize);
  }

  onResize(): void {
    if (window.innerWidth > 1264) {
      this.rePaddingL = "1%";
      this.rePaddingR = "2%";
    } else {
      this.rePaddingL = "5%";
      this.rePaddingR = "6%";
    }
  }
}
