











import { Chart, registerables } from 'chart.js';
import { Component, Vue} from 'vue-property-decorator';

Chart.register(...registerables)

@Component({
  name: 'PolarAreaChart',
})
export default class PolarAreaChart extends Vue {
  fillData(): Chart<"polarArea", number[], string> {
    const canvas = document.getElementById('polaareachart') as HTMLCanvasElement
    const ctx = canvas.getContext("2d") as CanvasRenderingContext2D
    var mychart = new Chart(ctx, {
      type: "polarArea",
      data: {
        labels: [
          'Red',
          'Green',
          'Yellow',
          'Grey',
          'Blue'
        ],
        datasets: [{
          borderWidth: 1,
          data: [11, 16, 7, 3, 14],
          borderRadius: 1,
          backgroundColor: [
          'rgb(255, 99, 132)',
          'rgb(75, 192, 192)',
          'rgb(255, 205, 86)',
          'rgb(201, 203, 207)',
          'rgb(54, 162, 235)'
          ]
        }],
      },
      options: {
        maintainAspectRatio: false,
        layout: {
          padding: 12
        },
        plugins: {
          legend: {
            position: 'bottom',
            title: {
              display: true,
              text: '',
              padding: 1
            },
            labels: {
              font: {
                size: 15,
              }
            }
          }
        },
        scales: {
        },
        elements: {
        }
      }
    })

    return mychart
  }

  mounted(): void {
    this.fillData()
  }
}

