// eslint-disable-next-line
declare var require: any

@Component({
  name: "HumandeepNav",
  components: {},
})
export default class HumandeepNav extends Vue {
  mainLogoWhite = require("@/assets/HD_logo/logo_white.png")

  gohome(): void {
    this.$router.push("/")
  }

  scrollPosition = 0;
  navColor = 'transparent';

  updateScroll(): void {
    this.scrollPosition = window.scrollY;
    if (this.scrollPosition>=10) {
      this.navColor = '#2D5DA6';
    } else {
      this.navColor = 'transparent';
    }
  }

  mounted(): void{
    window.addEventListener('scroll', this.updateScroll);
  }

