




















































































































































































































































import { Component, Vue } from "vue-property-decorator";
import axios from "axios";

import Text01 from "./mainText/Text01.vue";
import Text02 from "./mainText/Text02.vue";
import Text03 from "./mainText/Text03.vue";

// eslint-disable-next-line
declare var require: any

@Component({
  name: "HumandeepMain",
  components: {
    Text01,
    Text02,
    Text03,
  },
})
export default class HumandeepMain extends Vue {
  carouselImages = [
    {
      src: require("@/assets/hompageImage/휴먼딥-메인_수정_01.png"),
    },
    {
      src: require("@/assets/hompageImage/02_01.png"),
    },
    {
      src: require("@/assets/hompageImage/03_01.png"),
    },
  ];

  icon_a = require("@/assets/hompageImage/휴먼딥-메인_수정_04.png");
  icon_b = require("@/assets/hompageImage/휴먼딥-메인_수정_06.png");
  icon_c = require("@/assets/hompageImage/휴먼딥-메인_수정_08.png");

  snackbar = false;
  timeout = 1500;
  text = '내용을 입력하세요.';

  type = '문의';
  name = '';
  phone = '';
  email = '';
  contents = '';
  files = null;

  datacheck(): void {
    this.text = '현재 지원하지 않는 기능입니다. 하단의 메일 또는 전화 문의 바랍니다.'
    this.snackbar = true;
    // 백엔드 연결 후 설정
    // if (!this.name) {
    //   this.text = '이름 혹은 기관명을 입력하세요.'
    //   this.snackbar = true;
    // } else if (!this.phone) {
    //   this.text = '연락처를 입력하세요.'
    //   this.snackbar = true;
    // } else if (!this.email) {
    //   this.text = '이메일을 입력하세요.'
    //   this.snackbar = true;
    // } else if (!this.contents) {
    //   this.text = '문의내용을 입력하세요.'
    //   this.snackbar = true;
    // } else if (this.type && this.name && this.phone && this.email && this.contents) {
    //   this.sendemail();
    // }
  }

  sendemail(): void {
    const formdata = new FormData();
    formdata.append('type', this.type);
    formdata.append('name', this.name);
    formdata.append('phone', this.phone);
    formdata.append('email', this.email);
    formdata.append('contents', this.contents);
    if (this.files != null) {
      formdata.append('file', this.files as any);
    } else {
      formdata.append('file', '');
    }

    
    let config = {
      headers: {
        'Content-Type': 'multipart/form-data', // Content-Type을 변경해야 파일이 전송됨
        }
    }
    axios.post('/homepage/contact/', formdata, config)
      .then(() => {
        this.type = '';
        this.name = '';
        this.phone = '';
        this.email = '';
        this.contents = '';
        this.files = null;
        this.text = '전송 완료'

        this.snackbar = true;
      })
      .catch(() => {
        this.text = '전송 실패'

        this.snackbar = true;
      })
  }
}
