/* eslint-disable */
import axios from "axios";

const skinCancerStore = {
  state: {},
  mutations: {},
  actions: {
    classification_single(context: any, payload: Record<string, File>): Promise<unknown> {
      return new Promise((resolve) => {
        const image = new FormData;
        image.append("singleimage", payload.image);
        const config = {
          headers: {
            "Content-Type": "multipart/form-data", // Content-Type을 변경해야 파일이 전송됨
          }
        }
        
        axios.post("classification/single_test", image, config)
          .then((res) => {
            resolve(res.data)
          })
          .catch(() => {
            alert("모델 안돌았습니다.");
          })
      })
    },

    classification_multi(context: any, payload: Record<string, string[]>): Promise<unknown> {
      return new Promise((resolve) => {
        const image = new FormData();
        for (let i = 0; i < payload.image.length; i++) {   // 파일이 하나 이상인 경우
          image.append("multiimage", payload.image[i]);
        }
        const config = {
          headers: {
            "Content-Type": "multipart/form-data", // Content-Type을 변경해야 파일이 전송됨
          }
        }

        axios.post("classification/multi_test", image, config)
          .then((res) => {
            resolve(res.data);
          })
          .catch(() => {
            alert("모델 안돌았습니다.");
          });
      });
    },

    segmentation_single(context: any, payload: Record<string, File>): Promise<unknown> {
      return new Promise((resolve) => {
        const image = new FormData;
        image.append("singleimage", payload.image);
        const config = {
          headers: {
            "Content-Type": "multipart/form-data", // Content-Type을 변경해야 파일이 전송됨
          }
        };

        axios.post("classification/sg_single_test", image, config)
          .then((res) => {
            resolve(res.data)
            console.log(res.data)
          })
          .catch(() => {
            alert("모델 안돌았습니다.")
          })
      })
    },

    segmentation_multi(context: any, payload: Record<string, string[]>): Promise<unknown> {
      return new Promise((resolve) => {
        const image = new FormData();
        for (let i = 0; i < payload.image.length; i++) {   // 파일이 하나 이상인 경우
          image.append("multiimage", payload.image[i])
        }
        const config = {
          headers: {
            "Content-Type": "multipart/form-data", // Content-Type을 변경해야 파일이 전송됨
          }
        };

        axios.post("classification/sg_multi_test", image, config)
          .then((res) => {
            console.log("res.data", res.data)
            resolve(res.data)
          })
          .catch(() => {
            alert("모델 안돌았습니다.")
          });
      });
    },
  },
}

export default skinCancerStore
