


































import { Component, Prop, Vue } from 'vue-property-decorator';

@Component({
  name: "HuIonBox",
})
export default class HuIonBox extends Vue {
  @Prop() private bTop!: number
  @Prop() private bLeft!: number
  @Prop() private bWidth!: number
  @Prop() private bHeight!: number
  // eslint-disable-next-line
  @Prop() private onSelect!: any
  @Prop() private bActive!: boolean
  @Prop() private bIndex!: number
  // eslint-disable-next-line
  @Prop() private onDelete!: any
  @Prop() private bLabel!: string

  removeMySelf(): void {
    this.onDelete(this.bIndex)
  }
  selectBox(): void {
    this.onSelect(this.bIndex)
  }
}
