









import { Chart, registerables } from 'chart.js';
import { Component, Vue} from 'vue-property-decorator';

Chart.register(...registerables)

@Component({
  name: 'DoughnutnutChart',
})
export default class DoughnutChart extends Vue {
  fillData(): Chart<"doughnut", number[], string> {
    const canvas = document.getElementById('doughnutchart') as HTMLCanvasElement
    const ctx = canvas.getContext("2d") as CanvasRenderingContext2D
    var mychart = new Chart(ctx, {
      type: "doughnut",
      data: {
        labels: [
          'Red',
          'Blue',
          'Yellow'
        ],
        datasets: [{
          backgroundColor: [
            'rgb(255, 99, 132)',
            'rgb(54, 162, 235)',
            'rgb(255, 205, 86)'
          ],
          hoverOffset: 4,
          data: [ 2, 4, 5 ],
        }],
      },
      options: {
        maintainAspectRatio: false,
        layout: {
          padding: 12
        },
        plugins: {
          legend: {
            position: 'bottom',
            title: {
              display: true,
              text: '',
              padding: 1
            },
            labels: {
              font: {
                size: 15,
              }
            }
          }
        },
        scales: {
        },
        elements: {
        }
      }
    })

    return mychart
  }

  mounted(): void {
    this.fillData()
  }
}

