import Vue from 'vue'
import VueRouter, { Route } from 'vue-router'

import hugo from '@/views/Hugo.vue'
import HugoMain from '@/components/hugo/HugoMain.vue'
import HugoInfo from '@/components/hugo/infos/HugoInfo.vue'
import HugoNotice from '@/components/hugo/infos/HugoNotice.vue'
import HuSisInfo from '@/components/hugo/infos/HuSisInfoInHugo.vue'
import HuScerInfo from '@/components/hugo/infos/HuScerInfoInHugo.vue'

import sepsisIndex from './sepsis/HuSisIndex'
import skincancerIndex from './lab/skinCancer/skincancerIndex'
import labtempPredictionIndex from './lab/temperaturePrediction/temperatureIndex'
import labimageAnnotationIndex from './lab/imageAnnotation/HuionIndex'
import labsepsis from './lab/sepsis/SepsisIndex'

const hugoGard = (to: Route, from: Route, next: (url: string | void | undefined) => void): void => {
  // console.log(from.name, '여기서 와서')
  // console.log(to.name, '로 간다.')
  // console.log('=========')
  if (to.name === "hugo") {
    next('/hugo/main')
  } else {
    next()
  }
}

Vue.use(VueRouter)

export default [
  ...sepsisIndex,
  ...labtempPredictionIndex,
  ...labimageAnnotationIndex,
  ...labsepsis,
  ...skincancerIndex,

  {
    path: '/hugo',
    name: 'hugo',
    component: hugo,
    beforeEnter: hugoGard,
    // beforeEnter: onlyAuthUser,
    children: [
      { path: 'main', name: 'HugoMain', component: HugoMain },
      { path: 'info', name: 'HugoInfo', component: HugoInfo },
      { path: 'notice', name: 'HugoNotice', component: HugoNotice },
      { path: 'sepsis/info', name: 'SepsisInfo', component: HuSisInfo },
      { path: 'skincancer/info', name: 'SkincancerInfo', component: HuScerInfo },
    ],
  },
]