import Vue from "vue";
// eslint-disable-next-line
import VueRouter, { Route } from "vue-router";
import labSepsis from "@/components/hugo/lab/sepsis/sepsis.vue";
import labSepsisDashboard from "@/components/hugo/lab/sepsis/dashboard/sepsisDashboard.vue";


const labSepsisMainGuard = (to: Route, from: Route, next: (url: string | void | undefined) => void): void => {
  if (to.name === "labSepsis") {
    next("/hugo/lab/sepsis/dashboard")
  } else {
    next()
  }
}

Vue.use(VueRouter)

export default [
  {
    path: "/hugo/lab/sepsis",
    name: "labSepsis",
    component: labSepsis,
    beforeEnter: labSepsisMainGuard,
    children: [
      { 
        path: "dashboard", 
        name: "labSepsisDashboard", 
        component: labSepsisDashboard 
      },
    ]
  },
]