





























































































































































































































import { Component, Prop, Vue } from "vue-property-decorator";
import { mapState } from "vuex";
// eslint-disable-next-line
import router from "vue-router";
// eslint-disable-next-line
import store from "vuex";


@Component({
  name: "HuSisNav",
  computed: {
    ...mapState("husisStore", ["alert"]),
    trainAlert: {
      set: function() {
        this.$store.commit("setTrainAlert");
      },
      get: function() {
        return this.$store.state.husisStore.train_alert;
      }
    },
  }
})
export default class HuSisNav extends Vue{
  // window size에 따른 v-navigation-drawer on/off ()
  @Prop() public sideOn!: { type: boolean };
  
  // v-navigation-drawer 열고 닫기
  drawer = false;

  // sidebar footer setting
  window = {
    height: 0
  }

  handleResize(): void {
    this.window.height = window.innerHeight;
  }

  // window size에 따른 main body padding 변화
  // 좌 우 다르게 줌 (스크롤 ui 때문)
  rePaddingL = "1%";
  rePaddingR = "2%";

  created(): void {
    this.onResize()
    window.addEventListener("resize", this.onResize);

    // sidebar > "메인 돌아가기" > 높이 자동 설정 
    window.addEventListener("resize", this.handleResize);
    this.handleResize();
  }

  onResize(): void {
    if (window.innerWidth > 1264) {
      this.rePaddingL = "1%";
      this.rePaddingR = "2%";
      this.drawer = false; // 화면 커지면 자동 닫힘
    } else {
      this.rePaddingL = "5%";
      this.rePaddingR = "6%";
    }
  }

  sideClose(): void {
    this.drawer = false;
  }

  // side-bar 요소
  spseisItems = [
    { title: "대시보드", icon: "mdi-view-dashboard", router: "/sepsis/dashboard" },
    { title: "환자관리", icon: "mdi-account-supervisor", router: "/sepsis/patientmanage" },
    { title: "혈액검사결과", icon: "mdi-water", router: "/sepsis/bloodtest" },
    { title: "인공지능관리", icon: "mdi-chart-donut", router: "/sepsis/aimanage" },
  ];
  
  utilItems = [
    { title: "chart", icon: "mdi-chart-pie", router:"/sepsis/precharts" },
  ];

  hugoItems = [
    { title: "Sepsis", icon: "mdi-alpha-h-box", router:"/main/sepsis" },
    { title: "Hugo", icon: "mdi-alpha-h-box", router:"/main/hugo" },
    { title: "Humandeep", icon: "mdi-alpha-h-box", router:"/humandeep" },
  ]
  
  // nav-bar의 hugo 회원 기능
  isAdmin = false
  userInfo = ""
  menuItems = {
    "admin": [
      {title: "Users"},
      {title: "Main"},
      {title: "Hi"},
    ],
    "notAdmin": [
      {title: "Main"},
      {title: "Hi"},
    ]
  }
  accountItems = [
    { title: "My Profile" },
    { title: "Logout" }
  ]
  
  mounted(): void {
    this.$store.commit("setWebsocket");
    this.$store.dispatch("getMessage");
    // let localUerInfo = JSON.parse(localStorage.getItem("userInfo") || ")
    // if (localUerInfo) {
    //   this.userInfo = localUerInfo
    // }
  }

  menuMethod(title: string):void {
    if (title === "Main") {
      this.$router.push({path: "/hugo/main"})
    } else if (title === "Users") {
      this.$router.push({name: "Users"})
    } else if (title === "Hi") {
      alert("Hi 클릭")
    }
  }

  accountMethod(index: number):void {
    if (index === 0) {
      alert("마이프로필 동작")
    } else if (index === 1) {
      this.logout()
    }
  }

  logout(): void {
    this.$store.dispatch("logout")
      .then((res: boolean): void => {
        if (res === true) {
          this.$router.push({name: "Accounts"})
        }
      })
      .catch((err) => {
        alert(err.response.data.message)
      })
  }
}
