











import { Chart, registerables } from 'chart.js';
import { Component, Vue} from 'vue-property-decorator';

Chart.register(...registerables)

@Component({
  name: 'BarLineChart',
})
export default class BarLineChart extends Vue {
  fillData(): Chart<"bar" | "line", number[], string> {
    const canvas = document.getElementById('barlinechart') as HTMLCanvasElement
    const ctx = canvas.getContext("2d") as CanvasRenderingContext2D
    var mychart = new Chart(ctx, {
      type: "bar",
      data: {
        labels: ['A', 'B', 'C', 'D', 'E', 'F' ],
        datasets: [
        {
          label: 'data 1',
          borderWidth: 1,
          borderColor: [
            'RGB(255, 170, 173)',
          ],
          backgroundColor: [
            'rgba(255, 170, 173, 0.2)',
          ],
          data: [65, 59, 80, 81, 56, 55],
          order: 1
        },
        {
          label: 'data 2',
          type: "line",
          order: 2,
          borderColor: 'RGB(87, 175, 249)',
          data: [26, 49, 70, 36, 89, 51],
        }],
      },
      options: {
        maintainAspectRatio: false,
        plugins: {
          legend: {
            display: true,
          },
          tooltip: {
            backgroundColor: '#ffffff',
            borderColor: '#616161',
            borderWidth: .5,
            padding: {
              left: 15,
              right: 15,
              top: 8,
              bottom: 8,
            },
            caretSize: 0,
            // title
            titleColor: '#616161',
            titleAlign: 'center',
            titleMarginBottom: 15,
            titleFont: {
              size: 16
            },
            // body
            bodyColor: '#616161',
            bodyAlign: 'center',
            bodyFont: {
              size: 14
            },
            usePointStyle: true,
            callbacks: {
              labelPointStyle: function() {
                return {
                  pointStyle: 'circle',
                  rotation: 0
                }
              },
            }
          }
        },
        scales: {
          x: {
            grid: {
              drawBorder: false,
            },
            ticks: {
              padding: 8,
              font: {
                size: 13,
              }
            },
          },
          y: {
            beginAtZero: true,
            suggestedMax: 10,
            grid: {
              drawBorder: false,
              lineWidth: function(context) {
                if (context.tick.value == 0) {
                  return 3
                } else {
                  return .5
                }},
              color: function(context) {
                if (context.tick.value == 0) {
                  return "rgba(158, 176, 186, 1)"
                } else {
                  return "rgba(158, 176, 186, 0.4)"
                }},
            },
          },
        }
      }
    })

    return mychart
  }

  mounted(): void {
    this.fillData()
  }
}

