











import { Chart, registerables } from 'chart.js';
import { Component, Vue} from 'vue-property-decorator';

Chart.register(...registerables)

@Component({
  name: 'BarChart',
})
export default class BarChart extends Vue {
  fillData(): Chart<"bar", number[], string> {
    const canvas = document.getElementById('barcharthorizontal') as HTMLCanvasElement
    const ctx = canvas.getContext("2d") as CanvasRenderingContext2D
    var mychart = new Chart(ctx, {
      type: "bar",
      data: {
        labels: ['A', 'B', 'C', 'D', 'E', 'F' ],
        datasets: [
        {
          label: 'data 1',
          borderWidth: 1,
          borderColor: [
            'rgb(255, 99, 132)'
          ],
          backgroundColor: [
            'rgb(255, 99, 132, 0.2)',
          ],
          data: [65, -59, -80, 81, -56, 55],
        }],
      },
      options: {
        indexAxis: 'y',
        maintainAspectRatio: false,
        plugins: {
          legend: {
            display: true
          },
          tooltip: {
            backgroundColor: '#ffffff',
            borderColor: '#616161',
            borderWidth: .5,
            padding: {
              left: 15,
              right: 15,
              top: 8,
              bottom: 8,
            },
            caretSize: 0,
            // title
            titleColor: '#616161',
            titleAlign: 'center',
            titleMarginBottom: 15,
            titleFont: {
              size: 16
            },
            // body
            bodyColor: '#616161',
            bodyAlign: 'center',
            bodyFont: {
              size: 14
            },
            usePointStyle: true,
            callbacks: {
              labelPointStyle: function() {
                return {
                  pointStyle: 'circle',
                  rotation: 0
                }
              },
            }
          }
        },
      }
    })

    return mychart
  }

  mounted(): void {
    this.fillData()
  }
}

