



















































import { Component, Vue } from "vue-property-decorator";
import vueAnkaCropper from "vue-anka-cropper";
import "@/../node_modules/vue-anka-cropper/dist/VueAnkaCropper.css";
import HuIonCropSideBar from "./HuIonCropSideBar.vue";

@Component({
  name: "HuIonBoard",
  components: {
    vueAnkaCropper,
    HuIonCropSideBar
	},
})
export default class HuIonBoard extends Vue {
  croppedHeight = 400
  croppedWidth = 400
  // eslint-disable-next-line
  initData: Record<string, any> = {}
  existResult = false
  croppedImageURL: string|undefined = ""
  w = 0
  h = 0

  save(event: Record<string, undefined>):void {
    this.existResult = false
    this.initData = event
    // this.initData["label"] = this.label
    this.croppedImageURL = event.croppedImageURI
    delete this.initData.croppedImageURI
    
    // image W, H calc
    var img = new Image()
    img.src = String(this.croppedImageURL)
    
    setTimeout(() => {
      this.w = img.width
      this.h = img.height
      this.initData["W_pixel"] = this.w
      this.initData["H_pixel"] = this.h
      this.existResult = true
    }, 800);
  }

  // 새로고침 방지
  created(): void {
    document.onkeydown = this.lockF5
  }
  beforeDestroy():void {
    document.onkeydown = this.unlockF5
  }

  lockF5(event: KeyboardEvent): boolean|undefined {
    if (event.keyCode == 116) {
      alert('Not allowed')
      return false
    }
  }
  unlockF5(event: KeyboardEvent): boolean|undefined {
    if (event.keyCode == 116) {
      return true
    }
  }
}
