











import { Chart, registerables } from 'chart.js';
import { Component, Vue} from 'vue-property-decorator';

Chart.register(...registerables)

@Component({
  name: 'FloatingBarChart',
})
export default class FloatingBarChart extends Vue {
  fillData(): Chart<"bar", number[] | number[][], string> {
    const canvas = document.getElementById('floatingbarchart') as HTMLCanvasElement
    const ctx = canvas.getContext("2d") as CanvasRenderingContext2D
    var mychart = new Chart(ctx, {
      type: "bar",
      data: {
        labels: ['A', 'B', 'C', 'D', 'E' ],
        datasets: [
        {
          label: 'data 1',
          backgroundColor: [
            'rgb(255, 99, 132)',
          ],
          data: [
            [10, 20],
            [15, 40],
            [5, 30],
            [24, 50],
            [42, 60],
          ],
        },
        {
          label: 'data 2',
          backgroundColor: [
            'rgb(54, 162, 235)'
          ],
          data: [
            [17, 5],
            [32, 52],
            [25, 18],
            [40, 12],
            [52, 13],
          ],
        }],
      },
      options: {
        maintainAspectRatio: false,
        plugins: {
          legend: {
            display: true,
          },
          tooltip: {
            backgroundColor: '#ffffff',
            borderColor: '#616161',
            borderWidth: .5,
            padding: {
              left: 15,
              right: 15,
              top: 8,
              bottom: 8,
            },
            caretSize: 0,
            // title
            titleColor: '#616161',
            titleAlign: 'center',
            titleMarginBottom: 15,
            titleFont: {
              size: 16
            },
            // body
            bodyColor: '#616161',
            bodyAlign: 'center',
            bodyFont: {
              size: 14
            },
            usePointStyle: true,
            callbacks: {
              labelPointStyle: function() {
                return {
                  pointStyle: 'circle',
                  rotation: 0
                }
              },
            }
          }
        },
        scales: {
          x: {
            grid: {
              drawBorder: false,
            },
            ticks: {
              padding: 8,
              font: {
                size: 13,
              }
            },
          },
          y: {
            beginAtZero: true,
            suggestedMax: 10,
            grid: {
              drawBorder: false,
              lineWidth: function(context) {
                if (context.tick.value == 0) {
                  return 3
                } else {
                  return .5
                }},
              color: function(context) {
                if (context.tick.value == 0) {
                  return "rgba(158, 176, 186, 1)"
                } else {
                  return "rgba(158, 176, 186, 0.4)"
                }},
            },
          },
        }
      }
    })

    return mychart
  }

  mounted(): void {
    this.fillData()
  }
}

