









































































































import { Component, Vue } from "vue-property-decorator";

@Component({
  name: "SepsisNav", 
})
export default class SepsisNav extends Vue {
  // nav-bar의 hugo 회원 기능
  isAdmin = false
  userInfo = ""
  menuItems = {
    "admin": [
      {title: 'Users'},
      {title: 'Main'},
      {title: 'Hi'},
    ],
    "notAdmin": [
      {title: 'Main'},
      {title: 'Hi'},
    ]
  }
  accountItems = [
    { title: 'My Profile' },
    { title: 'Logout' }
  ]
  
  mounted(): void {
    // let localUerInfo = JSON.parse(localStorage.getItem("userInfo") || '')
    // if (localUerInfo) {
    //   this.userInfo = localUerInfo
    // }
  }

  menuMethod(title: string):void {
    if (title === 'Main') {
      this.$router.push({name: "Annotations"})
    } else if (title === 'Users') {
      this.$router.push({name: "Users"})
    } else if (title === "Hi") {
      alert('Hi 클릭')
    }
  }

  accountMethod(index: number):void {
    if (index === 0) {
      alert('마이프로필 동작')
    } else if (index === 1) {
      this.logout()
    }
  }
  
  logout(): void {
    this.$store.dispatch('logout')
      .then((res: boolean): void => {
        if (res === true) {
          this.$router.push({name: 'Accounts'})
        }
      })
      .catch((err) => {
        alert(err.response.data.message)
        this.$router.push({name: 'Accounts'})
      })
  }
}
