









































































































































































import { Component, Vue } from "vue-property-decorator";
import HugoNavCategory from "./HugoNavCategory.vue";
// import axios from "axios"

@Component({
  name: "HugoNavbar",
  components: {
    HugoNavCategory,
  },
})
export default class HugoNavbar extends Vue {
  drawer =  false;
  isAdmin = false;
  userInfo = "";
  menuItems = {
    "admin": [
      {title: "Users"},
      {title: "Main"},
      {title: "Hi"},
    ], 
    "notAdmin": [
      {title: "Main"},
      {title: "Hi"},
    ]
  };
  accountItems = [
    { title: "My Profile" },
    { title: "Logout" }
  ];

  prodectItems = [
    { title: "Husis", url: "/hugo/sepsis/info", explane: "패혈증 예측" },
  ];

  labItems = [
    { title: "Temperature Prediction ", url: "/hugo/temperaturePrediction", explane: "일교차 예측" },
    { title: "Image Annotation", url: "/hugo/lab/imageAnnotation", explane: "이미지 어노테이션" },
    { title: "Sepsis Prediction", url: "/hugo/lab/sepsis", explane: "패혈증 예측" },
    { title: "Skin Cancer", url: "/hugo/lab/skincancer", explane: "피부암 이미지 분석" },
  ]
  

  mounted(): void {
    // let localUerInfo = JSON.parse(localStorage.getItem("userInfo") || "")
    // if (localUerInfo) {
    //   this.userInfo = localUerInfo
    // }
  }

  menuMethod(title: string): void {
    var url = window.location.href
    var loc = url.split("/")
    if (title === "Main" && loc[3] !== "main") {
      this.$router.push({name: "Annotations"})
    } else if (title === "Users" && loc[3] !== "users") {
      this.$router.push({name: "Users"})
    } else if (title === "Hi") {
      alert("Hi 클릭")
    }
  }

  accountMethod(index: number): void {
    if (index === 0) {
      alert("마이프로필 동작")
    } else if (index === 1) {
      this.logout()
    }
  }

  logout(): void {
    this.$store.dispatch("logout")
      .then((res: boolean): void => {
        if (res === true) {
          this.$router.push({name: "Accounts"})
        }
      })
      .catch((err) => {
        alert(err.response.data.message)
      })
  }
}
