



























































































import { Component, Vue } from "vue-property-decorator";
import HuSisResultDetail from "./HuSisBloodDetail.vue";

@Component({
    name: "BloodTest",
    components: {
      HuSisResultDetail,
    }
})
export default class HuSisBloodTest extends Vue {
  search = "";
  headers = [
    { text: "환자번호", align: "start", sortable: true, value: "id" },
    { text: "환자이름", filterable: true, sortable: false, value: "name" },
    { text: "나이", filterable: false, sortable: false, value: "age" },
    { text: "성별", filterable: false, sortable: false, value: "sex" },
    { text: "검사번호", filterable: true, value: "blood_id" },
    { text: "검사시간", filterable: true, value: "time" },
    { text: "BaseExcess", filterable: false, sortable: false, value: "base_excess" },
    { text: "HCO3", filterable: false, sortable: false, value: "hco3" },
    { text: "Creatine", align: "center", filterable: false, sortable: false, value: "creatinine" },
    { text: "WBC", align: "center", filterable: false, sortable: false, value: "wbc" },
    { text: "Troponinl", align: "center", filterable: false, sortable: false, value: "troponinl" },
    { text: "Platelets", align: "center", filterable: false, sortable: false, value: "platelets" },
    { text: "전체 확인", align: "center", filterable: false, sortable: false, value: "bt_detail" },
  ];
    
  // window size에 따른 main body padding 변화
  // 좌 우 다르게 줌 (스크롤 ui 때문)
  rePaddingL = "1%";
  rePaddingR = "2%";

  created(): void {
    this.onResize();
    window.addEventListener("resize", this.onResize);
  }

  onResize(): void {
    if (window.innerWidth > 1264) {
      this.rePaddingL = "1%";
      this.rePaddingR = "2%";
    } else {
      this.rePaddingL = "5%";
      this.rePaddingR = "6%";
    }
  }

}
