


















































import { Component, Vue, Prop, Watch } from "vue-property-decorator";

@Component({
  name: "HuSisAIHistory",
  components: {
  }
})
export default class HuSisAIHistory extends Vue{
  @Prop() public addHistoryData!: { date: string; perform: number; usedData: number; f1Score: number; loss: number; }

  // 모델 이력 테이블 헤더
  historyHeaders = [
    { text: "date", sortable: false, align: "center", value: "date" },
    { text: "perform", sortable: false, align: "center", value: "perform" },
    { text: "usedData", sortable: false, align: "center", value: "usedData" },
    { text: "f1Score", sortable: false, align: "center", value: "f1Score" },
    { text: "loss", sortable: false, align: "center", value: "loss" },
  ]
    
  // 모델 이력 테이블 내용
  modelHistory = [
    { date: "21.08.30", perform: 65, usedData: 60, f1Score: 0.75, loss: 0.4231 },
    { date: "21.09.30", perform: 61, usedData: 70, f1Score: 0.78, loss: 0.3468 },
    { date: "21.10.30", perform: 75, usedData: 80, f1Score: 0.82, loss: 0.5321 },
    { date: "21.11.30", perform: 79, usedData: 90, f1Score: 0.80, loss: 0.3982 },
    { date: "21.12.30", perform: 80, usedData: 100, f1Score: 0.81, loss: 0.4312 },
  ]

  // 모델 이력 추가
  @Watch("addHistoryData")
  pushHistory(): void {
    this.modelHistory.push(this.addHistoryData);
  }
}
