



























































import { Component, Vue} from 'vue-property-decorator';
import axios, { AxiosResponse } from 'axios'

interface response {
  message: string,
}

@Component({
  name: 'FindEmail',
})

export default class FindEmail extends Vue {
  $refs!: {
    form: HTMLFormElement,
  }
  valid = true
  email = ''

  emailCheck(): void {
    // console.log(this.$refs.form)
    if (this.email === '') {
      alert('Please enter your E-mail.')
    } else if (this.$refs.form._data.inputs[0].validations[0] === 'E-mail must be reqeired & valid') {
      alert('This E-mail is not valid.')
    } else {
      // console.log({ email: this.email })
      axios.get(`accounts/email?email=${this.email}`)
        .then((response: AxiosResponse<response>) => {
          alert(response.data.message)
        })
    }
  }

  // @Watch('email')
  // private emailcheck(): void {
  //   console.log(this.$refs.form._data.inputs[0].validations[0])
  // }
}

