const getCoursorLeft = (e: any): number => {  // wdith
  let content = document.querySelector('#image-wrapper')
  if (content) {
    let x = content.getBoundingClientRect() ? content.getBoundingClientRect().left : 0
    return e.clientX - Math.round(x)
  }
  return 0
}
// eslint-disable-next-line
const getCoursorTop = (e: any): number => {   // height
  let content = document.querySelector('#image-wrapper')
  if (content) {
    let y = content.getBoundingClientRect() ? content.getBoundingClientRect().top : 0
    return e.clientY - Math.round(y) 
  }
  return 0
}

import { Component, Vue } from "vue-property-decorator";
import HuIonBoundingBoxSideBar from "./HuIonBoundingBoxSideBar.vue";
import HuIonBoundingBox from "./HuIonBoundingBox.vue";
import { pick } from "lodash";

@Component({
  name: "HuiBoundingBoxBoard",
  components: {
    HuIonBoundingBoxSideBar,
    HuIonBoundingBox,
  },
})
export default class HuIonBoundingBoxBoard extends Vue {
  image = null
  url = ""
  originalImageWidth = 0
  originalImageHeight = 0
  originalRatio = 0
  imageWidth = 0
  imageHeight = 0
  ratioWidth = "100%"
  ratioHeight = "400px"
  setMaxWidth = 1080
  setMaxHeight = 700
  result = {}

  // boundingBox 좌표 박스
  drawingBox = {
    active: false,
    top: 0,
    left: 0,
    height: 0,
    width: 0,
  }

  boxes:Array<Record<string, unknown>> = []
  saveToJsonBoxes:Array<Record<string, unknown>> = []
  activeBoxIndex = null
  dot = 0

  saveToTextFile():void {
    for (let i = 0; i < this.boxes.length; i++) {
      if (this.boxes[i]['label']) {
        this.saveToJsonBoxes[i]['label'] = this.boxes[i]['label']
      } else {
        this.saveToJsonBoxes[i]['label'] = null
      }
    }
    // eslint-disable-next-line
    const refs = this.$refs.image as any
    const name = refs.value.name
    
    for (let i = name.length; i > -1; i--) {
      console.log(name[i])
      if (name[i] == ".") {
        console.log(i)

        this.dot = i
        break
      }
    }
    const imageFileTitle = name.slice(0, this.dot)

    const text = JSON.stringify(this.saveToJsonBoxes)
    const textFileName = `${imageFileTitle}_Label`
    const element = document.createElement('a')
    element.setAttribute('id', 'createText')
    element.setAttribute('href', 'data:text/plain;charset=utf-8, ' + encodeURIComponent(text))
    element.setAttribute('download', textFileName)
    element.click()

    setTimeout(() => {
      const afterDownload = document.getElementById('createText')
      if (afterDownload) {
        afterDownload.remove()
      }
    }, 200);
  } 


  imageRatioCalc():void {
    // 비율에 맞는 v-img WH setting
    if (this.originalRatio === 1) {
      // alert('1:1 비율의 이미지로 확인')
      this.ratioWidth = "700px"
      this.ratioHeight = "700px"
      // this.ratioWidth = `${(700*this.originalRatio)}`+'px'
    } else if (this.originalRatio > 1) {
      // 가로가 긴 이미지 
      // alert('가로가 긴 이미지로 확인')
      if (this.originalImageWidth > this.setMaxWidth) {
        // alert('가로가 긴데 1080 보다 크다')
        // 가로가 긴데 1080보다 커? 이제 height 본다.
        // 가로이미지와 v-img 비율R 계산 후
        // 비율R이 높이안에 들어오나? 아닌가?
        if (this.originalImageHeight <= this.setMaxHeight) {
          // alert('가로가 긴데 높이가 700px보다 작다')
          // 높이 안에 들어오면
          let ratio = Number((this.setMaxWidth / this.originalImageWidth).toFixed(2))
          this.ratioWidth = `${this.originalImageWidth * ratio}` + "px"
          this.ratioHeight = `${this.originalImageHeight * ratio}` + "px"
        } else {
          // alert('가로가 긴데 높이가 700px보다 크다.')
          // 이미지가 v-img보다 상하가 다 크다??
          var vImg = 1.54
          var OImg = Number((this.originalImageWidth/this.originalImageHeight).toFixed(2))
          // alert(OImg)
          if (OImg > vImg ) {
            // width에 맞추면 됨
            // alert('width에 맞추차')
            let ratio = Number((1080 / this.originalImageWidth).toFixed(2))
            this.ratioWidth = `${this.originalImageWidth * ratio}` + "px"
            this.ratioHeight = `${this.originalImageHeight * ratio}` + "px"
          } else {
            // height에 맞추면 됨.
            // alert('height에 맞추차')
            let ratio = Number((700 / this.originalImageHeight).toFixed(2))
            // alert(ratio)
            this.ratioWidth = `${this.originalImageWidth * ratio}` + "px"
            this.ratioHeight = `${this.originalImageHeight * ratio}` + "px"
          }
          // let ratio =  Number((this.setMaxHeight / this.originalImageHeight).toFixed(2))
          // this.ratioWidth = `${this.originalImageWidth * ratio}` + "px"
          // this.ratioHeight = `${this.originalImageHeight * ratio}` + "px"
        }
      } else {
        // alert('가로가 긴데 1080보다는 작은 이미지로 확인 > 원본 사이즈로 출력')
        // 이미지 원본 사이즈 그대로 노출
        this.ratioWidth = `${this.originalImageWidth}` + "px"
        this.ratioHeight = `${this.originalImageHeight}`  + "px"
      }
    } else {
      // alert('세로(높이)가 긴 이미지로 확인')
      if (this.originalImageHeight > this.setMaxHeight) {
        // 세로가 긴데 700px 보다 큰 경우
        // alert('세로가 긴데 700px이상이다.')
        let ratio =  Number((this.setMaxHeight / this.originalImageHeight).toFixed(2))
        this.ratioWidth = `${this.originalImageWidth * ratio}` + "px"
        this.ratioHeight = `${this.originalImageHeight * ratio}` + "px"
      } else {
        // 세로가 긴데 700px 보다 작은 경우
        // alert('세로가 긴데 700px 이하다. > 원본 사이즈로 출력')
        this.ratioWidth = `${this.originalImageWidth}` + "px"
        this.ratioHeight = `${this.originalImageHeight}`  + "px"
      }
    }
  }

  previewImage(): void {
    this.boxes = []
    this.saveToJsonBoxes = []
    if (this.image != null) {
      this.url = URL.createObjectURL(this.image)
    }
    
    var image = document.createElement('img');
    var image_url = this.url
    image.src = image_url;
    image.onload = () => {
      // alert('이미지 온로드 동작')
      this.originalImageWidth = image.width; 
      this.originalImageHeight = image.height;
      this.originalRatio = Number((this.originalImageWidth / this.originalImageHeight).toFixed(2))
    }

    setTimeout(() => {
      var div = document.getElementById('image-wrapper')
      // console.log(div)
      if (div !== null) {
        this.imageWidth = div.clientWidth
        this.imageHeight = div.clientHeight
      }
      // 이미지 비율 확인
      this.imageRatioCalc()
    }, 300);
  }

  removeBox(i: number|undefined): void {
    this.boxes = this.boxes.filter((elem, index) => {
      return index !== i
    })
    this.saveToJsonBoxes = this.saveToJsonBoxes.filter((elem, index) => {
      return index !== i
    })
    this.activeBoxIndex = null;
  }
  // eslint-disable-next-line
  makeBoxDisabled(i: any): void {
    this.activeBoxIndex = null
  }

  // eslint-disable-next-line
  makeBoxActive(i: any): void {
    this.activeBoxIndex = i
  }

  // eslint-disable-next-line
  startDrawingBox(e: any): void {
    this.drawingBox = {
      active: true,
      width: 0,
      height: 0,
      top: getCoursorTop(e),
      left: getCoursorLeft(e),
    }
  }
  // eslint-disable-next-line
  changeBox(e: any): void {
    // console.log(getCoursorLeft(e), getCoursorTop(e) )
    if (this.drawingBox.active) {
      this.drawingBox = {
        ...this.drawingBox,
        height: getCoursorTop(e) - this.drawingBox.top,
        width: getCoursorLeft(e) - this.drawingBox.left,
      }
    }
  }
  stopDrawingBox(): void {
    if (this.drawingBox.active) {
      if (this.drawingBox.width > 1) {
        // 보여줄 BoundingBox 리스트
        this.boxes.push(
          {...pick(this.drawingBox, ['width', 'height', 'top', 'left'])}
        )

        // px 변환 후 label 저장 BoundingBox
        let W = Number((this.originalImageWidth/(Number(this.ratioWidth.slice(0, -2)))).toFixed(2))
        this.drawingBox.left = Number((this.drawingBox.left * W).toFixed(3))
        this.drawingBox.width = Number((this.drawingBox.width * W).toFixed(3))

        let H = Number((this.originalImageHeight/(Number(this.ratioHeight.slice(0, -2)))).toFixed(2))
        this.drawingBox.top = Number((this.drawingBox.top * H).toFixed(3))
        this.drawingBox.height = Number((this.drawingBox.height * H).toFixed(3))
        this.saveToJsonBoxes.push(
          {...pick(this.drawingBox, ['width', 'height', 'top', 'left'])}
        )
      }
      this.drawingBox = {
        active: false,
        top: 0,
        left: 0,
        height: 0,
        width: 0
      }
    }
  }
}
