import Vue from 'vue'
import VueRouter, { RouteConfig } from 'vue-router'
// 초기 로그인 화면
// import Accounts from '../views/Accounts.vue'
import FindEmail from '@/components/accounts/Findemail.vue'
import FindPassword from '@/components/accounts/Findpassword.vue'
import Signup from '@/components/accounts/Signup.vue'
import ResetPassword from '@/components/accounts/ResetPassword.vue'
import LoadingPage from '@/components/loadingPage/LoadingPage.vue'
import Users from '@/components/admin/Users.vue'

//Service List
import humandeepIndex from './humandeep/humandeepIndex'
import hugoIndex from './hugo/HugoIndex'

// eslint-disable-next-line
import { onlyAuthUser, Visiter, onlyAdmin } from '@/router/navigationGuard'

Vue.use(VueRouter)


const routes: Array<RouteConfig> = [
  ...hugoIndex,
  ...humandeepIndex,
  {
    path: '/users',
    name: 'Users',
    component: Users,
    // beforeEnter: onlyAdmin,
  },
  {
    path: '/loading',
    name: 'LoadingPage',
    component: LoadingPage,
  },
  {
    path: '/resetpassword',
    name: 'ResetPassword',
    component: ResetPassword,
    props: true,
  },
  
  {
    path: '/signup',
    name: 'Signup',
    component: Signup,
    // beforeEnter: Visiter
  },
  {
    path: '/findpassword',
    name: 'FindPassword',
    component: FindPassword,
    // beforeEnter: Visiter
  },
  {
    path: '/findemail',
    name: 'FindEmail',
    component: FindEmail,
    // beforeEnter: Visiter
  },
  // 초기 개발시 root 접속시 바로 로그인 화면 이동
  // Accounts는 로그인 화면
  // {
  //   path: '/',
  //   name: 'Accounts',
  //   component: Accounts,
  //   // beforeEnter: Visiter
  // },
]



const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior () {
    return { x: 0, y: 0 }
  }
})

export default router
  