





















import { Component, Vue } from "vue-property-decorator";
import HuSisNav from "./HuSisNav.vue"
import HuSisSidebar from "./HuSisSideBar.vue";


@Component({
  name: "HuSisMain",
  components: {
    HuSisNav,
    HuSisSidebar,
  },
})
export default class HuSis extends Vue{
  // window size에 따른 side-bar on/off
  sideOn = true;

  created(): void {
    this.onResize()
    window.addEventListener("resize", this.onResize);
  }

  onResize(): void {
    if (window.innerWidth > 1264) {
      this.sideOn = true;
    } else {
      this.sideOn = false;
    }
  }
}
