/* eslint-disable */
import axios from "axios"
import { Commit, Dispatch } from "vuex";

interface Patient {
  id: number;
  name: string;
  age: number;
  sex: string;
  adm_time: number;
  iculos: number;
  room: string;
  bed: string;
  reg_date: Date;
  reg_id: string;
  upd_date: Date;
  upd_id: string;
  emp_id: string;
  ai_detect: number;
}

interface Vital {
  id: number;
  time: Date;
  hr: number;
  o2sat: number;
  temp: number;
  sbp: number;
  map: number;
  dbp: number;
  resp: number;
  etco2: number;
  train: string;
  patient_id: number;
}

interface Blood {
  id: number;
  time: Date;
  base_excess: number;
  hco3: number;
  fio2: number;
  k: number;
  ph: number;
  total_co2: number;
  paco2: number;
  pao2_fio2: number;
  sao2: number;
  ast: number;
  bun: number;
  alkalinephos: number;
  calcium: number;
  chloride: number;
  creatinine: number;
  direct_bilirubin: number;
  glucose: number;
  lactate: number;
  magnesium: number;
  phosphate: number;
  potassium: number;
  total_bilirubin: number;
  troponinl: number;
  hct: number;
  hgb: number;
  ptt: number;
  wbc: number;
  fibrinogen: number;
  platelets: number;
  train: string;
  alert: string;
  patient_id: number;
}

interface PatientInfo {
  state: string;
  list: Array<any>;
  total: number;
}

const husisStore = {
  state: {
    alert : [false, false, false],
    message: ["","",""],
    trainAlert : false,
    trainMessage : "",
    // websocket: new WebSocket("ws:127.0.0.1:8080/ws/"),
    // websocket: new WebSocket("ws:192.168.0.12:8080/ws/"),
    patients: [{ id: 0, ai_detect: 0 }],
    patientInfo: [  // 대시보드: 환자 현황 데이터
      {
        state: "위험",
        list: [],
        total: 0,
      },
      {
        state: "의심",
        list: [],
        total: 0,
      },
      {
        state: "안전",
        list: [],
        total: 0,
      },
    ],
    vitalProp: 0, // 환자의 상세 정보를 클릭했는지 구분
    vitals: [{  // 전체 vital 데이터
      id: 0,
      time: "2022-01-01",
      hr: 0,
      o2sat: 0,
      temp: 0,
      sbp: 0,
      map: 0,
      dbp: 0,
      resp: 0,
      etco2: 0,
      train: "X",
      patient_id: 0
    }],
    vitalDetail: [{ // 클릭한 확자의 vital 데이터
      id: 0,
      time: "2022-01-01",
      hr: 0,
      o2sat: 0,
      temp: 0,
      sbp: 0,
      map: 0,
      dbp: 0,
      resp: 0,
      etco2: 0,
      train: "X",
      patient_id: 0
    }],
    vitalData: [{}], // 환자 데이터 + 가장 최근 vital 데이터
    bloods: [{id: 0, patient_id: 0, train: "T", time: "2022-01-01"}], //혈액 데이터
    bloodData: [{}],  // 환자 데이터 + 가장 최근 혈액 데이터
  },
  getters: {
    // 환자 수 출력
    patientsCount: (state: { patients: Array<Patient> }): number => {
      return state.patients.length
    },
    // AI 예측 결과가 없는 환자 수
    uncheckedPatientsCount: (state: {patients: Array<Patient>}): number => {
      let count = 0;
      state.patients.forEach(patient => {
        if (patient.ai_detect === 0) count++
      })
      return count
    },
    // AI 학습에 포함되지 않은 데이터 수
    untrainedDataCount: (state: { bloods: Array<Blood> }): number => {
      let count = 0;
      state.bloods.forEach(blood => {
        if (blood.train === "F") count++
      })
      return count
    },
  }, 
  mutations: {
    // websocket 연결
    setWebsocket: (state: { websocket: WebSocket }): void => {
      state.websocket.close();
      // state.websocket = new WebSocket("ws:127.0.0.1:8080/ws/");
      state.websocket = new WebSocket("ws:192.168.0.12:8080/ws/");
    },
    // 미학습 데이터 알림 설정
    setTrainAlert: (state: { trainAlert: boolean }): void => {
      state.trainAlert = false;
    },
    // 서버로부터 온 메시지 설정
    changeMessage: (state: { trainAlert: boolean, trainMessage: string, alert: Array<boolean>, message: Array<string> }, payload: { data: string }): void => {
      if (payload.data.includes("미학습")) {
        state.trainAlert = true;
        state.trainMessage = payload.data;
      } else {
        state.alert.shift();
        state.alert.push(true);
        state.message.shift();
        state.message.push(payload.data);
      }
    },
    // 환자 정보 설정
    setPatients: (state: { patients: Array<Patient> }, payload: Array<Patient>): void => {
      state.patients = payload;
    },
    // 대시보드: 환자 현황 데이터 설정
    setPatientInfo: (state: { patients: Array<Patient>, patientInfo: Array<PatientInfo> }): void => {
      const danger = [];
      const doubt = [];
      const safety = [];
      for (const patient of state.patients) {
        if (patient.ai_detect > 75) {
          danger.push(patient.name);
        } else if (patient.ai_detect > 45) {
          doubt.push(patient.name);
        } else {
          safety.push(patient.name);
        }
      };
      state.patientInfo[0].list = danger;
      state.patientInfo[0].total = danger.length;
      state.patientInfo[1].list = doubt;
      state.patientInfo[1].total = doubt.length;
      state.patientInfo[2].list = safety;
      state.patientInfo[2].total = safety.length;
    },
    // vital 정보 설정
    setVitals: (state: { vitals: Array<Vital> }, payload: Array<Vital>): void => {
      state.vitals = payload;
    },
    // 클릭한 환자에 대한 vital 정보 설정
    setVitalDetail: (state: { vitalDetail: Array<Vital> }, payload: Array<Vital>): void => {
      state.vitalDetail = payload;
    },
    setVitalData: (state: { patients: Array<Patient>, vitals: Array<Vital>, vitalData: Array<any> }): void => {
      if (state.vitals.length != 0) {
        state.vitalData = [];
        const patientsData = state.patients;
        for (const patient of patientsData) {
          let vitals = state.vitals.filter(it => it.patient_id === patient.id);
          vitals = vitals.sort(function(a, b) {
            return new Date(a.time).getTime() - new Date(b.time).getTime();
          });
          const vital = vitals[vitals.length - 1];
          const vitalId = vital.id;
          type objType = {[index: string]: string|number|unknown}
          const temp: objType = {};
          const keys = Object.keys(vital);
          const values = Object.values(vital);
          let count = 0;
          for (const i of keys) {
            if (i === "id") {
              count++
            } else {
              temp[i] = values[count]
              count++
            }
          }
          Object.assign(temp, {"vital_id": vitalId})
          Object.assign(patient, temp);
          state.vitalData.push(patient);
        }
      } else {
        state.vitalData = [];
      }
    },
    // vital 정보가 클릭되어 있을 때, 클릭한 환자에 대한 vital 정보 설정
    autoSetVitalDetail: (state: { vitalDetail: Array<Vital>, vitalProp: number, vitals: Array<Vital> } ): void => {
      state.vitalDetail = [];
      if (state.vitalProp > 0) {
        for (const vital of state.vitals) {
          if (vital.patient_id === state.vitalProp) {
            state.vitalDetail.push(vital);
          }
        }
      }
    },
    // 혈액 정보 설정
    setBloods: (state: { bloods: Array<Blood> }, payload: Array<Blood>): void => {
      state.bloods = payload;
    },
    // 환자 데이터 + 가장 최근 혈액 데이터 설정
    setBloodData: (state: { patients: Array<Patient>, bloods: Array<Blood>, bloodData: Array<any> }): void => {
      if (state.bloods.length != 0) {
        state.bloodData = [];
        const patientsData = state.patients;
        for (const patient of patientsData) {
          let bloods = state.bloods.filter(it => it.patient_id === patient.id);
          bloods = bloods.sort(function(a, b) {
            return new Date(a.time).getTime() - new Date(b.time).getTime();
          });
          const blood = bloods[bloods.length - 1];
          const bloodId = blood.id;
          type objType = {[index: string]: string|number|unknown}
          const temp: objType = {};
          const keys = Object.keys(blood);
          const values = Object.values(blood);
          let count = 0;
          for (const i of keys) {
            if (i === "id") {
              count++
            } else {
              temp[i] = values[count]
              count++
            }
          }
          Object.assign(temp, {"blood_id": bloodId})
          Object.assign(patient, temp);
          state.bloodData.push(patient);
        }
      } else {
        state.bloodData = [];
      }
    },
  },
  actions: {
    // 서버로부터 메시지 수신 => 환자, vital, 혈액 정보 가져와서 저장
    getMessage({ state, commit, dispatch }: { state: {websocket: WebSocket}, commit: Commit, dispatch: Dispatch }): void {
      state.websocket.onmessage = (res) => {
        commit("changeMessage",res);
        dispatch("getPatients")
          .then(() => {
            commit("setPatientInfo");
            dispatch("getBloods")
              .then(() => {
                commit("setBloodData");
              });
          });
        dispatch("getVitals")
          .then(() => {
            commit("setVitalData");
            commit("autoSetVitalDetail");
          });
      }
    },
    // 서버로부터 환자 정보 수신
    getPatients({ commit }: { commit: Commit }): Promise<Patient|string> {
      return new Promise((resolve) => {
        axios.get("husis/patient")
          .then((res) => {
            resolve(res.data);
            commit("setPatients", res.data);
          })
          .catch((err) => {
            alert(err);
          })
      })
    },
    // 서버로부터 vital 정보 수신
    getVitals({ commit }: { commit: Commit }): Promise<Vital|string>{
      return new Promise((resolve) => {
        axios.get("husis/vital")
          .then((res) => {
            resolve(res.data);
            commit("setVitals", res.data);
          })
          .catch((err) => {
            alert(err);
          })
      })
    },
    // 서버로부터 혈액 정보 수신
    getBloods({ commit }: { commit: Commit }): Promise<Blood|string> {
      return new Promise((resolve) => {
        axios.get("husis/blood")
          .then((res) => {
            resolve(res.data);
            commit("setBloods", res.data);
          })
          .catch((err) => {
            alert(err);
          })
      })
    },
    // 임시로 학습
    setTrained({ dispatch }: { dispatch: Dispatch }): Promise<string> {
      return new Promise((resolve) => {
        axios.post("husis/blood/")
          .then((res) => {
            resolve(res.data);
            dispatch("getBloods")
          })
          .catch((err) => {
            alert(err);
          })
      })
    },
  },
};

export default husisStore
