







































































































import { Component, Vue} from 'vue-property-decorator';
import axios, { AxiosResponse } from 'axios'

interface res {
  message: string,
  result: boolean,
  auth_code: string,
}

@Component({
  name: 'FindPassword'
})

export default class FindPassword extends Vue {
  valid = false
  email = ""
  isAuthCodeIssued = false
  minute = 0
  second = 0
  isAuthTimerRunning = false
  // 인증번호 검증
  returnAuthCode = ""
  inputAuthCode = ""


  AuthTimer(from: number, to: number): void {
    this.isAuthTimerRunning = true
    var current = from
    var timer = setInterval(() => {
      this.minute = Math.floor(current/60)
      this.second = current%60
      // console.log(Math.floor(current/60), current%60)
      if (current == to) {
        clearInterval(timer); //clearInterval는 setInterval을 멈추게 한다.
        this.isAuthCodeIssued = false
      }
      current--
      if (!this.isAuthTimerRunning) {
        clearInterval(timer)
      }
    }, 1000)
  }

  confirmAuthCode(): void {
    if (this.returnAuthCode !== this.inputAuthCode) {
      this.inputAuthCode = ""
      alert('Incorrect Auth-Code.')
    } else {
      alert('Confirm Auth-Code.')
      this.isAuthTimerRunning = false
      this.$router.push({name: 'ResetPassword', params: {email: this.email}})
    }
  } 

  authCodeIssue(): void {
    if (this.email !== "") {
      axios.post('accounts/password', {email: this.email})
        .then((res: AxiosResponse<res>) => {
          // console.log(res)
          this.isAuthCodeIssued = res.data.result
          this.returnAuthCode = res.data.auth_code
          this.AuthTimer(300, 0)
        })
        .catch((err) => {
          alert(err.response.data.message)
          // console.log(err.response)
        })
    } else {
      alert("Please Enter your E-mail.")
    }
  }
}

