





















































































import { Component, Vue, Watch } from "vue-property-decorator";
// eslint-disable-next-line
import store from "vuex";

@Component({
  name: "Segmentation",
})
export default class Segmentation extends Vue {
  loading = false;
  image = null;
  url = ""; // 업로드 이미지 url
  testabled = true; // 테스트 가능=false/불가능=true
  resultShow = false; // 결과 보여주기

  // 업로드한 이미지가 있을 경우 테스트 가능하게 해줌
  @Watch("image")
  imageChange(): void {
    if (this.image != null) {
      this.url = URL.createObjectURL(this.image);
      this.testabled = false;
    } else {
      this.testabled = true
    }
  }

  // 모델 테스트
  test(): void {
    this.resultShow = true;
    this.loading = true;
    this.$store.dispatch("segmentation_single", { image: this.image })
    // eslint-disable-next-line
      .then((res: any) => {
        this.pixelimgdecoding(res.pixel_img)
        console.log(res)
        this.loading = false
      })
  }

  // 픽셀 이미지 decoding
  pixelimgdecoding(res: string): void {
    var img = new Image();
    img.addEventListener("load", () => {
      // 캔버스에 그리기
      /* eslint-disable */
      var canvas = document.getElementById("pixelimg") as HTMLCanvasElement;
      var ctx2 = canvas.getContext("2d");
      ctx2!.drawImage(img, -400, 0);
    });
    img.src = "data:image/png;base64," + res.slice(1,-1)
  }
}
