













import { Component, Vue } from "vue-property-decorator";
import HuSisStatics from "./HuSisStatics.vue";
import HuSisCharts from "./HuSisCharts.vue";
import HuSisPatientInfo from "./HuSisPatientInfo.vue";
import HuSisBed from "./HuSisBed.vue";


@Component({
  name: "HuSisDashBoard",
  components: {
    HuSisStatics,
    HuSisCharts,
    HuSisPatientInfo,
    HuSisBed,
  },
})
export default class HuSisDashBoard extends Vue{
  // window size에 따른 main body padding 변화
  // 좌 우 다르게 줌 (스크롤 ui 때문)
  rePaddingL = "1%";
  rePaddingR = "2%";

  created(): void {
    this.onResize()
    window.addEventListener("resize", this.onResize);
  }

  onResize(): void {
    if (window.innerWidth > 1264) {
      this.rePaddingL = "1%";
      this.rePaddingR = "2%";
    } else {
      this.rePaddingL = "5%";
      this.rePaddingR = "6%";
    }
  }
}
