/* eslint-disable */
import axios from "axios";

const labsisStore = {
  state: {},
  getters: {},
  mutations: {},
  actions: {
    labSepsis(context: any, payload: Record<string, unknown>): Promise<unknown> {
      return new Promise((resolve, reject) => {
        axios.post("lab/sepsis", payload)
          .then((res) => {
            resolve(res)
          })
          .catch((err) => {
            console.log(err)
            reject(err)
          })
      })
    },
  },
};

export default labsisStore