








































import { Component, Vue } from "vue-property-decorator";
import HuSisStatic from "./HuSisAIStatic.vue";
import HuSisAIHistory from "./HuSisAIHistory.vue";
import HuSisCurruntAI from "./HuSisCurruntAI.vue";

interface ModelType {
  perform: number;
  usedData: number;
  f1Score: number;
  loss: number;
}

@Component({
  name: "HuSisAIManagement",
  components: {
    HuSisStatic,
    HuSisAIHistory,
    HuSisCurruntAI,
  }
})
export default class HuSisAIManagement extends Vue{

  // 현재 모델 정보
  curruntModel: ModelType = {
    perform: 80,
    usedData: 100,
    f1Score: 0.81,
    loss: 0.4312,
  }
  
  // AI History Prop Data
  addHistoryData = {};

  // Emit retrained AI model data 
  staticEmit(reTrainedModel: ModelType, date: string): void {
    var fakeData = { date: date, perform: reTrainedModel.perform, usedData: reTrainedModel.usedData, f1Score: reTrainedModel.f1Score, loss: reTrainedModel.loss }
    this.addHistoryData = fakeData
    this.curruntModel = reTrainedModel
  }

  // window size에 따른 main body padding 변화
  // 좌 우 다르게 줌 (스크롤 ui 때문)
  rePaddingL = "1%";
  rePaddingR = "2%";

  created(): void {
    this.onResize()
    window.addEventListener("resize", this.onResize);
  }

  onResize(): void {
    if (window.innerWidth > 1264) {
      this.rePaddingL = "1%";
      this.rePaddingR = "2%";
    } else {
      this.rePaddingL = "5%";
      this.rePaddingR = "6%";
    }
  }

}
