









import { Chart, registerables } from 'chart.js';
import { Component, Vue} from 'vue-property-decorator';

Chart.register(...registerables)

@Component({
  name: 'ScatterChart',
})
export default class ScatterChart extends Vue {
  fillData(): Chart<"scatter",  { x: number; y: number; }[], string> {
    const canvas = document.getElementById('scatterchart') as HTMLCanvasElement
    const ctx = canvas.getContext("2d") as CanvasRenderingContext2D
    var mychart = new Chart(ctx, {
      type: "scatter",
      data: {
        labels: [
          'Red',
          'Blue',
          'Yellow'
        ],
        datasets: [{
          data: [{
            x: -10,
            y: 0
          },
          {
            x: 0,
            y: 10
          },
          {
            x: 10,
            y: 5
          },
          {
            x: 0.5,
            y: 5.5
          },
          {
            x: -3,
            y: 3
          },
          {
            x: -6,
            y: 8
          },
          {
            x: 7,
            y: 1
          },
          {
            x: 4,
            y: 4
          },],
          backgroundColor: 'rgb(255, 99, 132)'
        }],
      },
      options: {
        maintainAspectRatio: false,
        plugins: {
          legend: {
            display: false
          }
        },
        scales: {
          x: {
              type: 'linear',
              position: 'bottom'
          }
        },
        elements: {
        }
      }
    })

    return mychart
  }

  mounted(): void {
    this.fillData()
  }
}

