











import VueApexCharts from "vue-apexcharts";
import { Component, Vue } from "vue-property-decorator";

var start = new Date("2020-01-01");
var end = new Date("2020-12-31");
var xDate = [] as string[];
var oneYearData = [] as number[];
var oneYearData2 = [] as number[];
var oneYearData3 = [] as number[];

while(start <= new Date(end)) {
  xDate.push(start.toISOString().split("T")[0]);
  start.setDate(start.getDate() + 1);
}

for (var i = 0; i < 366; i++) {
  if (i%3 === 0) {
    oneYearData.push(i)
    oneYearData2.push(2+i)
    oneYearData3.push(3-i)
  } else {
    oneYearData.push(-i)
    oneYearData2.push(2+i)
    oneYearData3.push(-3-i)
  }
}

@Component({
  name: "TempChart",
  components: { VueApexCharts },
})
export default class TempChart extends Vue {
  series = [
    {
      name: "max actual",
      type: "line",
      data: this.$store.state.tempPredicStore.max_temp,
      color: "#757575",
    },
    {
      name: "min actual",
      type: "line",
      data: this.$store.state.tempPredicStore.min_temp,
      color: "#757575",
    },
    {
      name: "max prediction",
      type: "line",
      data: this.$store.state.tempPredicStore.max_pred,
      color: "#E53935",
    },
    {
      name: "min prediction",
      type: "line",
      data: this.$store.state.tempPredicStore.min_pred,
      color: "#E53935",
    },
  ];
  chartOptions = {
    chart: {
      type: "line",
      zoom: {
        enabled: false,
      },
      animations: {
        enabled: false,
      },
      fontFamily: "Noto Sans KR, sans-serif",
      toolbar: {
        show: false,
      },
    },
    plotOptions: {
      bar: {
        columnWidth: "60%",
      },
    },
    dataLabels: {
      enabled: false,
    },
    stroke: {
      show: true,
      width: 2,
      dashArray: [0, 0],
    },
    labels: xDate,
    legend: {
      offsetY: 6,
    },
    markers: {
      size: 0,
    },
    xaxis: {
      type: "datetime",
      labels: {
        offsetY: -0.2,
      },
    },
    yaxis: {
      max: Math.max(...this.series[0].data) + 10,
      min: Math.min(...this.series[0].data) - 10,
      tickAmount: 8,
    },
    tooltip: {
      x: {
        show: true,
        formatter: undefined,
      },
      y: {
        formatter(val: number): number {
          return val;
        },
        title: {
          formatter(seriesName: string): string {
            return seriesName
          },
        },
      },
    },
  };
}
