/* eslint-disable */
import axios from "axios";

interface State {
  max_pred: number[];
  max_temp: number[];
  min_pred: number[];
  min_temp: number[];
}

const tempPredicStore = {
  state: {
    max_pred: [], // 최고온도 예측
    max_temp: [], // 최고온도 실제
    min_pred: [], // 최저온도 예측
    min_temp: [], // 최저온도 실제
  },
  getters: {},
  mutations: {
    // api 결과 입력
    get_temp_prediction_data: (state: State, res: State): void => {
      state.max_pred = res.max_pred;
      state.max_temp = res.max_temp;
      state.min_pred = res.min_pred;
      state.min_temp = res.min_temp;
    },

    // 데이터 초기화
    temp_prediction_date_reset: (state: State): void  => {
      state.max_pred = [];
      state.max_temp = [];
      state.min_pred = [];
      state.min_temp = [];
    },
  },
  actions: {
    // 도시 목록 가져오기
    get_temperature_cities(): Promise<unknown> {
      return new Promise((resolve): void => {
        axios.get("temperature/cities_data")
          .then((res) => {
            resolve(res.data);
          })
          .catch((err) => {
            console.log(err);
          });
      });
    },

    // ai model api
    post_city(context: any, payload: Record<string, string>): Promise<unknown> {
      return new Promise((resolve): void => {
        const data = payload;
        axios.post("temperature/input_city", data)
          .then((res) => {
            context.commit("get_temp_prediction_data", res.data);
            resolve(res.data);
          })
          .catch((err) => {
            console.log(err);
          });
      });
    },
  },
};

export default tempPredicStore
