













































































































































































































import { Component, Vue, Watch } from "vue-property-decorator";
import TempChart from "./TempChart.vue";
import TempBody from "./TempPredictionBody.vue";

@Component({
  name: "TempPredictionBody",
  components: {
    TempChart,
    TempBody,
  },
})
export default class TempPredictionBody extends Vue {
  diff_mae = 0; // 일교차 mae
  y_mae = 0; // 최고온도 mae
  y_mae_min = 0; // 최저온도 mae

  // 선택한 날짜 데이터
  max_pred_date = 0; // 최고온도 예측
  max_temp_date = 0; // 최고온도 실제
  min_pred_date = 0; // 최저온도 예측
  min_temp_date = 0; // 최저온도 실제

  modelList = [
    { name: "LSTM" },
    { name: "GRU" },
  ]

  loading = false; // progress
  selectModel = "선택한 모델";
  select = "선택한 지역";
  date = "2000-01-01";
  state = ""; // 주
  city = ""; // 도시
  aiModel = ""; // 모델
  all_lsit: Record<string, Array<string>> = {}; // api로 받은 주 + 도시 dict
  state_list: string[] = []; // 주 리스트
  city_list: string[] = []; // 도시 리스트

  // 모델 학습
  run(): void {
    this.loading = true;

    this.select = this.state + ", " + this.city;
    this.selectModel = this.aiModel;

    // 모델 학습 api
    this.$store.dispatch('post_city', { state: this.state, city: this.city, model: this.aiModel })
      .then((res) => {
        this.loading = false;
        this.diff_mae = res.diff_mae.toFixed(2);
        this.y_mae = res.y_mae.toFixed(2);
        this.y_mae_min = res.y_mae_min.toFixed(2);
      })
      .catch(() => {
        alert("모델 학습에 실패하였습니다.")
      })
  }

  // 선택, 학습 데이터 초기화
  reset(): void {
    this.$store.commit('temp_prediction_date_reset')
    this.loading = false;
    this.state = "";
    this.city = "";
    this.aiModel = "";
    this.select = "선택한 지역";
    this.selectModel = "선택한 모델"
  }

  mounted(): void {
    // 주, 도시 이름 불러오는 api
    this.$store.dispatch('get_temperature_cities')
      .then((res: Record<string, Array<string>>) => {
        this.all_lsit = res;
        for (var key in res) {
          this.state_list.push(key)
        }
      })
    this.reset();
  }

  // 주 + 도시 이름 만들기
  @Watch("state")
  get_cities(): void {
    this.city_list = this.all_lsit[this.state];
  }

  // 날짜에 맞는 데이터의 인덱스 구하기
  @Watch("date")
  pickDate(): void {
    var firstDate = new Date("2000-01-01");
    var picked = new Date(this.date);
    var day = 1000*60*60*24;

    const date1utc = Date.UTC(firstDate.getFullYear(), firstDate.getMonth(), firstDate.getDate());
    const date2utc = Date.UTC(picked.getFullYear(), picked.getMonth(), picked.getDate());

    var searchDate = (date2utc - date1utc)/day;

    this.max_pred_date = this.$store.state.tempPredicStore.max_pred[searchDate]
    this.min_pred_date = this.$store.state.tempPredicStore.min_pred[searchDate]
    this.max_temp_date = this.$store.state.tempPredicStore.max_temp[searchDate]
    this.min_temp_date = this.$store.state.tempPredicStore.min_temp[searchDate]
  }
}
