 /* eslint-disable */
import axios from "axios";
import router from "@/router";
import { Dispatch } from "vuex";

const loginStore = {
  state: {
    userInfo: {
      nickname: "User",
      email: "user@humandeep.co.kr",
      // email: "admin@humandeep.co.kr",
    },
  },
  actions: {
    access(): Promise<string> {
      return new Promise((resolve, reject) => {
        // alert("access 동작")
        const accessToken = localStorage.getItem("access-token")
        if (accessToken === null) {
          // alert("store access token이 없습니다.")
          reject(false)
        } else {
          axios.get("accounts/access", { headers: {"Authorization": `Bearer ${accessToken}`} })
            .then((res) => {
              // alert("store access-token에서 사용자를 인증했습니다.")
              // console.log("accessToken 인증 성공", res)
              resolve(res.data.messages.message)
            })
            .catch((err) => {
              // alert("store access-token에서 사용자를 인증하지 못했습니다.")
              // console.log("accessToken 인증 실패", err.response.data)
              reject(err.response.data.messages.message)
            })
        }
      })
    },

    refresh(): Promise<unknown> {
      return new Promise((resolve, reject) => {
        // alert("refresh 동작")
        const refreshToken = localStorage.getItem("refresh-token")
        if (refreshToken === null) {
          // alert("refresh token이 없습니다.")
          reject(false)
        } else {
          axios.post("accounts/api/token/refresh/", {refresh: refreshToken})
            .then((res) => {
              // console.log(res)
              // alert("refresh 토큰이 활성화 상태입니다.")
              localStorage.setItem("access-token", res.data.access)
              resolve(res)
            })
            .catch((err) => {
              // console.log(err.response)
              // alert("refresh 토큰이 비활성화 상태입니다.")
              localStorage.removeItem("access-token")
              localStorage.removeItem("refresh-token")
              localStorage.removeItem("userInfo")
              reject(err.response)
            })
        }
      })
    },

    logout({ dispatch }: { dispatch: Dispatch }): Promise<unknown> {
      return new Promise((resolve, reject) => {
        const accessToken = localStorage.getItem("access-token")
        const refreshToken = localStorage.getItem("refresh-token")
        axios.delete("accounts/logout", { data: {refresh: refreshToken}, headers: {"Authorization": `Bearer ${accessToken}`} })
          .then((res): void => {
            // console.log(res)
            localStorage.removeItem("access-token")
            localStorage.removeItem("refresh-token")
            localStorage.removeItem("userInfo")
            alert("Logout COMPLETE")
            resolve(res.data.result)
          })
          .catch(() => {
            // console.log(err)
            dispatch("refresh")
              .then(() => {
                // alert("로그아웃 refresh token 인증")
                // 다시 로그아웃
                dispatch("logout")
                  .then((res) => {
                    // alert("최종마무리")
                    console.log("최종 결과", res)
                    resolve(res)
                  })
              })
              .catch((err) => {
                // console.error(err)
                localStorage.removeItem("access-token")
                localStorage.removeItem("refresh-token")
                localStorage.removeItem("userInfo")
                reject(err)
              })
          })
      })
    },
    
    signin(context: any, payload: string): Promise<unknown> {
      // alert("로그인 동작")
      return new Promise((resolve, reject) => {
        axios.post("accounts/signin", payload)
        .then((res) => {
          router.push({name: "LoadingPage"})
          localStorage.setItem("access-token", res.data.access)
          localStorage.setItem("refresh-token", res.data.refresh)
          localStorage.setItem("userInfo", JSON.stringify(res.data.user_info))
          setTimeout(()=>{
            resolve(res.data.state)
          }, 800)
        })
        .catch((err) => {
          // alert("에러....?")
          reject(err)
        })
      })
    },
  },
}

export default loginStore
