






import { Chart, registerables } from "chart.js";
import { Component, Vue} from "vue-property-decorator";


Chart.register(...registerables)


@Component({
  name: "BarChart",
})
export default class BarChart extends Vue {
  fillData(): Chart<"bar", number[], string> {
    const canvas = document.getElementById("barchart") as HTMLCanvasElement
    const ctx = canvas.getContext("2d") as CanvasRenderingContext2D
    var mychart = new Chart(ctx, {
      type: "bar",
      data: {
        labels: ["위험", "의심", "안전", "미검사" ],
        datasets: [{
          borderWidth: 1,
          borderColor: [
            "#EF5350",
            "#F57C00",
            "#43A047",
            "#757575"
          ],
          backgroundColor: [
            "#FFCDD2",
            "#FFE0B2",
            "#C8E6C9",
            "#F5F5F5"
          ],
          data: [ 2, 9, 16, 1 ],
        }],
      },
      options: {
        maintainAspectRatio: false,
        plugins: {
          legend: {
            display: false
          },
          tooltip: {
            backgroundColor: "#ffffff",
            borderColor: "#616161",
            borderWidth: .5,
            padding: {
              left: 15,
              right: 15,
              top: 8,
              bottom: 8,
            },
            caretSize: 0,
            // title
            titleColor: "#616161",
            titleAlign: "center",
            titleMarginBottom: 15,
            titleFont: {
              size: 16
            },
            // body
            bodyColor: "#616161",
            bodyAlign: "center",
            bodySpacing: 10,
            bodyFont: {
              size: 14
            },
            usePointStyle: true,
            callbacks: {
              labelPointStyle: function() {
                return {
                  pointStyle: "circle",
                  rotation: 0
                }
              },
            }
          }
        },
        interaction: {
          mode: "index",
          intersect: false,
        },
        elements: {
          point: {
            radius: 4,
            hoverRadius: 8,
          }
        },
        scales: {
          x: {
            grid: {
              drawBorder: false,
            },
            ticks: {
              padding: 8,
              font: {
                size: 13,
              }
            },
          },
          y: {
            beginAtZero: true,
            suggestedMax: 10,
            grid: {
              drawBorder: false,
              lineWidth: function(context) {
                if (context.tick.value == 0) {
                  return 3
                } else {
                  return .5
                }},
              color: function(context) {
                if (context.tick.value == 0) {
                  return "rgba(158, 176, 186, 1)"
                } else {
                  return "rgba(158, 176, 186, 0.4)"
                }},
            },
            ticks: {
              stepSize: 2,
            },
          },
        }
      }
    })

    return mychart
  }

  mounted(): void {
    this.fillData()
  }
}

