













































































import { Component, Vue, Watch } from "vue-property-decorator";
// eslint-disable-next-line
import store from "vuex";

interface ResData {
  "mel" : number;
  "nv" : number;
  "bcc" : number;
  "akiec" : number; 
  "bkl" : number; 
  "df" : number;
  "vasc" : number;
}

@Component({
  name: "Classification",
})
export default class Classification extends Vue {
  resdata = {} as ResData;

  loading = false;
  image = null;
  url = ""; // 업로드 이미지 url
  testabled = true; // 테스트 가능=false/불가능=true
  resultShow = false; // 결과 보여주기

  resultHeaders = [
    { text: "name", value: "name" },
    { text: "percent", value: "percent"},
  ];

  result = [
    { name: "흑색종 (Melanoma)", percent: 0 },
    { name: "모반 (Melanocytic nevus)", percent: 0 },
    { name: "기저 세포 (Basal cell carcinoma)", percent: 0 },
    { name: "광선 각화증 (Actinic keratosis)", percent: 0 },
    { name: "지루 각화증 (Seborrheic keratosis)", percent: 0 },
    { name: "피부섬유종 (Dermatofibroma)", percent: 0 },
    { name: "혈관성병변 (Vascular lesion)", percent: 0 },
  ]

  // 업로드한 이미지가 있을 경우 테스트 가능하게 해줌
  @Watch("image")
  imageChange(): void {
    if (this.image != null) {
      this.url = URL.createObjectURL(this.image);
      this.testabled = false;
    } else {
      this.testabled = true;
    }
  }

  // 모델 테스트
  test(): void {
    this.resultShow = true;
    this.loading = true;
    this.$store.dispatch("classification_single", { image: this.image })
    // eslint-disable-next-line
      .then((res: ResData) => {
        this.resdata = res;
        this.result[0].percent = this.resdata.mel;
        this.result[1].percent = this.resdata.nv;
        this.result[2].percent = this.resdata.bcc;
        this.result[3].percent = this.resdata.akiec;
        this.result[4].percent = this.resdata.bkl;
        this.result[5].percent = this.resdata.df;
        this.result[6].percent = this.resdata.vasc;

        this.loading = false;
      })
  }
}
